* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html, body, navBar {
  position: relative;
  min-height: 100vh;
  max-width: 100vw;
  width: 100vw;
}

.logo {
  grid-column: 3;
  grid-row: 1;
  position: relative;
  right: 24vh;
  top: 7vh;
  font-size: 20px;
  transition: 1s;
  color: #ff3401;
  z-index: 9;
  position: fixed;
}

.logo:hover {
  letter-spacing: 30px;
  transition: 1s;
  z-index: 9;
}

navBar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 9999;
  background-image: url("./Images/universeBackground.jpg");
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  display: grid;
  grid-template-columns: repeat(20, 1fr);
  grid-template-rows: repeat(7, 100px);
}

.icon {
  position: absolute;
  z-index: 4;
  left: 5%;
  width: -moz-fit-content;
  width: fit-content;
  height: 44px;
  cursor: pointer;
  background-color: white;
  z-index: 100;
}

.hamburguer {
  top: 8vh;
  left: 10%;
  width: 30px;
  height: 6px;
  background: #ffffff;
  position: absolute;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: 1s;
  position: fixed;
  z-index: 100;
}

.hamburguer::before {
  top: -12px;
  content: "";
  position: absolute;
  width: 37px;
  height: 6px;
  background: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: 1s;
  z-index: 100;
}

.hamburguer::after {
  top: 12px;
  content: "";
  position: absolute;
  width: 37px;
  height: 6px;
  background: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: 1s;
  z-index: 100;
}

.list {
  display: flex;
  flex-direction: column;
  position: relative;
  left: 20%;
  padding: 145px 0 0;
  flex-wrap: nowrap;
  color: white;
  font-size: 2rem;
  font-weight: 600;
}

.listItems a {
  text-decoration: none;
  color: white;
  transition: 1s;
  transition-delay: 0.2s;
}

.listItems a:hover {
  letter-spacing: 40px;
  transition: 1s;
}

.list .listItems {
  padding: 0;
  list-style: none;
}

.list .listItems li {
  margin: 70px 8px;
  cursor: pointer;
}

.list .listItems li:hover {
  font-weight: 1500;
}

.list h4, .EntrarContato, .email {
  position: relative;
  left: 60vh;
  bottom: 65vh;
  font-size: 80%;
  color: black;
}

.list h4 {
  padding-bottom: 40px;
  width: 28%;
  font-size: 100%;
}

.EntrarContato {
  width: 30%;
  font-size: 70%;
}

.list .email {
  padding-top: 30px;
  font-size: 50%;
}

.email:hover {
  font-weight: bolder;
  font-size: 30px;
  transition: 1s;
}

.email {
  transition: 1s;
}

.icon.iconActive .hamburguer {
  background: transparent;
  box-shadow: 0 2px 5px transparent;
}

.icon.iconActive .hamburguer:after {
  top: 0;
  background-color: #fff;
  transform: rotate(225deg);
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
}

.icon.iconActive .hamburguer:before {
  top: 0;
  background-color: #fff;
  transform: rotate(135deg);
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
}

.menu.menuOpen {
  width: 204.5vh;
  height: 100vh;
  box-shadow: 10px 0px 69px 0px rgba(0, 0, 0, 0.59);
  transition: 2s;
  background: #bf2600;
  transition-delay: 0.2s;
  z-index: 99;
  position: fixed;
}

.menu.menuClose {
  width: 0;
  overflow: hidden;
  transition: 2s;
  height: 520vh;
}

.container {
  height: 100vh;
  width: 200vh;
  overflow-x: hidden;
  overflow-y: hidden;
  position: relative;
  top: 150px;
}

.carousel {
  display: flex;
  width: 100%;
  height: 100%;
  scroll-snap-type: x mandatory;
  overflow-x: scroll;
  overflow-x: hidden;
  scroll-behavior: smooth;
  overflow-y: hidden;
  max-width: 100%;
}

.item {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  scroll-snap-align: start;
  position: relative;
  color: white;
  text-align: center;
  top: 60%;
}

.photo {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.info {
  position: relative;
  bottom: 10%;
  left: 20%;
  padding: 20px;
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  text-align: center;
  width: 20%;
